export default{
    path: '/about/:id',
    component: () => import('@/components/About/index'),
    children:[
        {
            name:'pic',
            path : 'pic',
            props:true,
            component:() =>import('@/components/About/Pic/index'),
            // resolve => require(['../page/my/my.vue'], resolve),
            children:[
                {
                    path:'imgList',
                    name:'imgList',
                    props:true,
                    component:() => import('@/components/About/Pic/Pic_list')
                }
            ]
        },
        {
            name:'ask',
            path:'ask',
            props:true,
            component:() =>import('@/components/About/Ask')
        }
    ]
}