<template>
    <div id='CarSeries' @touchstart='start' @touchmove='move' @touchend='end'>
        <ul v-if="isShow">
            <li v-for="(item,i) in seriesCarlist" :key='i'>
                <div class='header'>
                    <p>{{item.GroupName}}</p>
                </div>
                <div v-for='(it,i) in item.GroupList' :key='i' class='body' @click='car_detail(it)'>
                    <div class="img">
                        <img :src="it.Picture" alt="" srcset="">
                    </div>
                    <div class="name">
                        <p>{{it.AliasName}}</p>
                        <p>{{it.DealerPrice}}</p>
                    </div>
                </div>
            </li>
        </ul>
        <Loading v-else />
    </div>
</template>
<script>
export default {
    name:'CarSeries',
    props:['id'],
    data(){
        return{
            seriesCarlist:new Array(),
            isShow:false,
            pageX:0,
            last_pageX:0,
            width:0
        }
    },
    mounted(){
        this.width = this.$el.clientWidth;
    },
    watch:{
        id(){
            if(typeof(this.id) != 'number'){
                this.$el.style.transform = 'translateX(100%)';
                return;
            }
            this.isShow = false;
            this.$el.style.transform = 'translateX(0)';
            this.axios.get('/car_api/v2-car-getMakeListByMasterBrandId.html?MasterID='+this.id).then(res => {
                if(res.status ===200 && res.data.code === 1){
                    this.seriesCarlist = res.data.data;
                    this.isShow = true;
                }
            })
        }
    },
    methods:{
        start(){
            this.pageX = event.changedTouches[0].pageX;
        },
        move(){
            let last_pageX = this.pageX - event.changedTouches[0].pageX;
            if(last_pageX <= 0){
                this.last_pageX = last_pageX;
                this.$el.style.right = this.pageX - event.changedTouches[0].pageX + 'px';
            }
        },
        end(){
            if(-this.last_pageX > this.width / 3){
                this.$el.style.transform = 'translateX(100%)';
                this.$emit('change_id','');
            }else{
                this.$el.style.transform = 'translateX(0)';
            }
            this.last_pageX = 0;
            this.$el.style.right = 0;
        },
        car_detail(it){
            this.$router.push('/about/'+it.SerialID);
        }
    }
}
</script>
<style lang='stylus' scoped>
    #CarSeries
        position absolute
        top 0
        right 0
        background-color white
        transform translateX(100%)
        transition all .3s ease-out
        height 100%
        overflow auto
        width 80%
        .header
            width 100%
            height 40px
            background-color #f4f4f4
            display flex
            justify-content space-between
            padding 12px
            align-items center
            .group
                font-size 16px
                color #222
        .body
            height 80px
            display flex
            img 
                width 120px
                height 100%
            .name
                padding 16px
                display flex
                flex-direction column
                justify-content space-between
                p:last-child
                    color #ed5a4e
</style>