<template>
    <div id='Home' v-if='is_show'>
        <div class='list'>
            <ul class='car' ref='car'>
                <li v-for="(it,key,i) in carList" :key='i'>
                    <p class='car_letter'>{{key}}</p>
                    <p class='car_name' v-for="(item,i) in it" :key='i' 
                    @click='to_car_detail(item.MasterID)'>
                        {{item.Name}}
                    </p>
                </li>
            </ul>
            <ul class='letter' @touchstart='to_letter' @touchmove='to_letter' @touchend='letter_'>
                <li data-i = '-1'>#</li>
                <li v-for="(it,key,i) in carList" :key='i' :data-i = 'i'>{{key}}</li>
            </ul>
        </div>
        <car-series :id='id' @change_id='change_id' />
        <router-view />
    </div>
    <Loading v-else />
</template>
<script>
import CarSeries from '@/components/CarSeries'
export default {
    name:'Home',
    data(){
        return {
            carList:'',
            offsetTop:new Array(),
            id:-1,
            is_show:false
        };
    },
    components:{
        CarSeries
    },
    created(){
        this.axios.get('/car_api/v2-car-getMasterBrandList.html').then(res => {
            console.log(res);
            if(res.status === 200 && res.data.code === 1){
                this.fit(res.data.data);
                this.is_show = true;
            }
        })
    },
    mounted(){
        this.$nextTick(()=>{
            setTimeout(() => {
                this.getOffsetTop();
            }, 1000);
        })
    },
    methods:{
        change_id(id){
            this.id = id;
        },
        getOffsetTop(){
            this.$refs.car && this.$refs.car.childNodes.forEach(e => {
                this.offsetTop.push(e.offsetTop);
            });
        },
        fit(list){
            if(this.offsetTop.length === 0){
                this.getOffsetTop();
            }
            let carList = {};
            list.forEach(e => {
                let letter = e.Spelling.slice(0,1);
                if(carList[letter]){
                    carList[letter].push(e);
                }else{
                    carList[letter] = new Array();
                    carList[letter].push(e);
                }
            });
            this.carList = carList;
        },
        to_letter(){
            if(event.target.tagName != 'UL' && event.target.dataset.i != -1){
                this.$refs.car.scrollTop = this.offsetTop[event.target.dataset.i];
            }else{
                this.$refs.car.scrollTop = 0;
            }
        },
        letter_(){//touchend
            
        },
        to_car_detail(id){
            this.id = parseInt(id);
        }
    }
}
</script>
<style lang='stylus' scoped>
    #Home
        position relative
        width 100%
        height 100%
        overflow-x hidden
        .list
            display flex
            align-items center
            width 100%
            height 100%
            top 0
            left 0
            .car
                position absolute
                width 100%
                height 100%
                overflow auto
                p
                    height 36px
                    line-height 36px
                    padding-left 20px
                .car_letter
                    width 100%
                    background-color #f4f4f4
                .car_name
                    border-bottom 1px solid #f4f4f4
        .letter
            position fixed
            right 6px
            line-height 26px
</style>